.App {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.App .header {
  width: 100%;
  height: 60px;
  padding: 8px 8px 8px 16px;
  box-sizing: border-box;
  background: #00a5e4;
  font-size: 1.2em;
  line-height: 44px;
  color: white;
}

button {
  height:38px;
  width:100px;
  border-radius:5px;
  background-color:rgb(1, 126, 173);
  border:0;
  outline: none;
  font-size:16px;
  color:#fff;
  float:right;
  cursor:pointer;
  transition-duration: 300ms;
}

button:hover {
  background:rgb(64, 149, 181);
}

.fileUpload {
  display: flex;
  width: 500px;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  height: 150px;
  border: 1px dotted green;
  margin: 20px auto;
}

ul {
  list-style: none;
  width:100%;
}

li {
  height:20px;
  padding:12px;
  background:#fff;
  border-bottom:1px solid #eee;
}

li:nth-child(odd) {
  background:#f4f4f4;
}

li a{
  text-decoration: none;
  font-weight: bold;
  color:#00a5e4;
}